.join{
    margin-top:-100px;
    height: 100%;
    padding:150px 0px;
    position: relative;
    overflow: hidden;
    background-image: url(../../../public/img/join01.jpg);
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat
    ;
    filter: saturate(50%);

    img{
        position: absolute;
        object-fit: cover;

        &:nth-of-type(1){
            width: 200px;
            height: 200px;
            opacity: 0;
            animation: joinAni 20s;
            transform: rotate(275deg);
        }

        &:nth-of-type(2){
            animation: joinAni2 30s linear infinite;
            width:25px;
            height: 25px;
        }

        &:nth-of-type(3){
            animation: joinAni3 22s linear infinite;
            width: 60px;
            height: 60px;
        }

        &:nth-of-type(4){
            width: 30px;
            height: 30px;
            opacity: 0;
            animation: joinAni4 20s 3s linear infinite;
        }

        &:nth-of-type(5){
            width: 70px;
            height: 70px;
            opacity: 0;
            animation: joinAni5 22s 3s linear infinite;
        }

        &:nth-of-type(6){
            width: 200px;
            height: 300px;
            filter: saturate(40%);
            opacity: 0;
            animation: joinAni6 12s 15s linear infinite;     
            transform: rotate(40deg);       
        }

        &:nth-of-type(7){
            width: 100px;
            height: 200px;
            filter: saturate(40%);
            opacity: 0;
            animation: joinAni7 10s 27s linear infinite;    
            transform: rotate(40deg);               
        }

        &:nth-of-type(8){
            width: 300px;
            height: 400px;
            opacity: 0;
            animation: joinAni8 25s 5s linear infinite;
        }

        &:nth-of-type(9){
            width: 100px;
            height: 200px;
            opacity: 0;
            animation: joinAni9 25s 5s linear infinite;
        }

        &:nth-of-type(10){
            width: 300px;
            height: 300px;
            opacity: 0;
            transform: rotate(230deg);
            animation: joinAni10 20s linear infinite;
            filter: saturate(30%);
        }
    }

    .inner{ 

        .joinTitle{
            background: rgba(0,0,0,.09);
            min-height: 300px;
            position: relative;
            box-shadow: 5px 5px 10px rgba(0,0,0,.3);

            .joinSns{
                width: 15%;
                display: flex;
                justify-content: space-around;
                position: absolute;
                right: 10px;
                top: 20px;
                color:#fff;
                cursor: pointer;
                font-size: 22.5px;
            }

            .joinContent{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                width: 80%;
                position: absolute;
                left: 10%;
                top: 10%;
                padding:15px 25px;

                &::before{
                    content: '';
                    display: block;
                    width: 5px;
                    height: 80%;
                    background: #f9bf7c;
                    position: absolute;
                    top: 10%;
                    left: -6.5%;
                }

                h1{
                    font:bold 45px/1 'Orbitron';
                    color:#f9bf7cc9;
                    letter-spacing: 2.3px;
                    margin-bottom: 30px;
                }

                p{
                    font:normal 10px/15px 'Orbitron';
                    color:#eee;
                    letter-spacing: 1.3px;
                    width: 50%;
                    margin-bottom: 30px;
                }

                span{
                    font:bold 10px/13px 'Orbitron';
                    color:#eee;
                    letter-spacing: 1.3px;
                    margin-bottom: 15px;
                }

                button{
                    width: 120px;
                    height: 30px;
                    border:none;
                    background: #f9bf7c;
                    font:bold 11px/13px 'Orbitron';
                    color:#333;
                    letter-spacing: 1.3px;
                    cursor: pointer;
                    transition: 1s;

                    &:hover{
                        background: rgba(0,0,0,.5);
                        color:rgba(255,255,255,.8);
                    }
                }
            }
        }

        .joinForm{
            background: #fff;
            padding-bottom: 0;
            box-shadow: 5px 5px 10px rgba(0,0,0,.3);

            .success{
                width: 100%;
                padding: 20px;
                background: lightcyan;
                font:bold 16px/1 ' ';
                color:cornflowerblue;
                margin-bottom: 50px;
            }
            
            table{
                width: 80%;
                margin-left: 10%;

                tbody{
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                }

                th,td{
                    border-bottom: 1px solid #ddd;
                    padding:50px 30px 15px;
                }

                .comment{
   
                    th{
                        line-height: 150px;
                        padding:30px;
                    }


                    td{
                        width: 770px;
                        padding:30px;

                        textarea{
                            width: 100%;
                            height: 150px;
                            padding:10px 20px;
                            border:1px solid #aaa;
                            resize: none;
                        }
                    }
                }
            
                th{
                    width: 170px;
                    height:130px;
                    text-align: left;
                    font:bold 10px/1 'Orbitron';
                    color:#555;
                    letter-spacing: 1px;
                    text-align: center;
                    line-height:35px;
            
                    &.btnSet{
                        text-align: center;
                        width: 940px;
                        padding:0px 30px;
                        height: 100px;
                        line-height: 100px;

                        input[type=reset], input[type=submit]{
                            width: 150px;
                            height: 40px;
                            text-align: center;
                            margin: 30px;
                            cursor: pointer;
                            transition: 0.5s;
                            border:none;
                            background: #f9bf7c;
                            font:bold 11px/13px 'Orbitron';
                            color:#333;
                            letter-spacing: 1.5px;
            
                            &:hover{
                                background: #555;
                                color:#fff;
                            }
                        }
                    }
                }

                td{
                    width: 300px;

                    input[type=text], input[type=password],select{
                        width: 100%;
                        padding:7px 20px;
                        border:1px solid #aaa;
                    }
        
                    label{
                        font:11px/1 'Orbitron';
                        color:#777;
                        margin-right: 3px;
                        letter-spacing: 1px;
                    }
            
                    input[type=checkbox], input[type=radio]{
                        margin-right: 20px;
                    }
            
                    .err{
                        font:bold 10px/13px 'Orbitron';
                        color:red;
                        display: block;
                        margin-top:5px;
                        letter-spacing: 1px;
                    }
                }
            }
        }
    }
}

@keyframes joinAni {
    0%{left:0px; top:0px; opacity: 0;}
    70%{left:70%; top:80%; opacity: 1;}
    95%{left:110%; top:110%; opacity: 0;}
    100%{left:0px; top:0px; opacity: 0;}
}

@keyframes joinAni2 {
    0%{left:10%; bottom:3%; opacity: 0;}
    70%{opacity: 0.5;}
    95%{left:30%; bottom:80%; opacity: 0;}
    100%{left:10%; bottom:3%; opacity: 0;}
}

@keyframes joinAni3 {
    0%{left:10%; bottom:0%; opacity: 0;}
    70%{opacity: 0.5;}
    95%{left:35%; bottom:80%; opacity: 0;}
    100%{left:10%; bottom:0%; opacity: 0;}
}

@keyframes joinAni4 {
    0%{right:10%; bottom:2%; opacity: 0;}
    70%{opacity: 0.5;}
    95%{right:20%; bottom:80%; opacity: 0;}
    100%{right:10%; bottom:2%; opacity: 0;}
}


@media screen and(max-width:1559px) {
    .join{
    
        .inner{ 
            width: 90%;
    
            .joinForm{

                table{
                    width: 95%;
                    margin-left: 2.5%;

                    tbody{
                         justify-content: center;
                        align-items: center;
                    }

                    th,td{padding:50px 10px 15px;}
                
                    th{
                        font:bold 12px/1 'Orbitron';
                        line-height:35px;
                
                        &.btnSet{
    
                            input[type=reset], input[type=submit]{ font:bold 12.5px/13px 'Orbitron';
                            }
                        }
                    }
    
                    td{
                        .err{
                            font:bold 11.5px/14px 'Orbitron';
                            letter-spacing: 1.2px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and(max-width:$tablet) { 
    .join{
    
        .inner{ 
            width: 95%;

            .joinTitle{
    
                .joinContent{
    
                    p{
                        overflow: hidden;
                        text-overflow: ellipsis;   
                        display: -webkit-box;
                        -webkit-line-clamp: 3; 
                        -webkit-box-orient: vertical;
                    }
                }
            }

            .joinForm{

                table{
                    width: 100%;
                    margin-left: 0%;

                    th{
                        font:bold 12px/1 'Orbitron';
                        line-height:35px;
                        width: 170px;
                        
                        &.btnSet{
    
                            input[type=reset], input[type=submit]{ font:bold 11px/13px 'Orbitron';
                            width: 135px;
                            height: 35px;
                            }
                        }
                    }
    
                    td{
                        width: 230px;

                        label{font:11px/1 'Orbitron'; letter-spacing: 0.5px;}

                        input[type=checkbox], input[type=radio]{
                            margin-right: 10px;
                        }
                    }

                    .comment{
   
                        th{
                            padding:30px 10px;
                        }
    
    
                        td{
                            padding:30px 10px;
                            width: 650px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 899px){
    .join{
    
        .inner{ 
            padding:50px 0px;

            .joinTitle{
               min-height: 250px;
                padding:0px;

                .joinContent{
                    top:5%;

                    h1{font-size: 40px;}
                }
            }

            .joinForm{

                table{
                    
                    tbody{padding-top: 25px;}

                   tr{
                       width: 100%;
                        padding:0px 30px;

                    &:nth-child(1){display: none;}
                    &:nth-child(6){display: none;}
                    &:nth-child(7){display: none;}
                    &:nth-child(8){display: none;}
                    &:nth-child(9){display: none;}

                    th,td{padding-top:45px; padding-bottom: 45px;}

                    th{
                        font:bold 12.5px/1 'Orbitron';
                        line-height:35px;
                        width: 200px;
                    }
    
                    td{width: 400px;}
                   }
                }
            }
        }
    }
}

@media screen and (max-width: 679px){
    .join{
    
        .inner{ 
            padding:0px;

            .joinTitle{
               min-height: 200px;
                padding:0px;

                .joinSns{display: none;}

                .joinContent{
                    top:3%;

                    h1{
                        font-size: 35px;
                        margin-bottom: 20px;
                    }

                    p{
                        -webkit-line-clamp: 2;
                        margin-bottom: 20px;
                    }
                }
            }

            .joinForm{

                table{

                   tr{

                    th{
                        &.btnSet{
    
                            input[type=reset], input[type=submit]{ font:bold 10.5px/13px 'Orbitron';
                            width: 120px;
                            height: 32.5px;
                            }
                        }
                    }
                   }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile){ 
    .join{
    
        .inner{ 

            .joinTitle{

                .joinSns{display: none;}

                .joinContent{

                    &::before{display: none;}                    

                    h1{
                        font-size: 35px;
                        margin-bottom: 20px;
                    }

                    p{
                        -webkit-line-clamp: 2;
                        margin-bottom: 20px;
                    }

                    button{
                        width: 100px;
                        height: 27px;
                    }
                }
            }

            .joinForm{

                table{

                    tbody{padding-top: 0;}

                   tr{
                    padding:0px 15px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &:nth-child(2),&:nth-child(3),&:nth-child(4),&:nth-child(5){th{display: none;}}

                    th{
                        &.btnSet{
                            padding:0px;
                            width: 100%;

                            input[type=reset], input[type=submit]{ font:bold 10px/13px 'Orbitron';
                            width: 100px;
                            height: 30px;
                            margin:10px;
                            }
                        }
                    }

                    td{width: 100%;}
                   }
                }
            }
        }
    }
}


@keyframes joinAni5 {
    0%{right:10%; bottom:0%; opacity: 0;}
    70%{opacity: 0.5;}
    95%{right:25%; bottom:80%; opacity: 0;}
    100%{right:10%; bottom:0%; opacity: 0;}
}

@keyframes joinAni6 {
    0%{right:0%; top:10%; opacity: 0;}
    70%{opacity: 0.7;}
    95%{right:40%; top:65%; opacity: 0;}
    100%{right:0%; top:10%; opacity: 0;}
}

@keyframes joinAni7 {
    0%{right:0%; top:5%; opacity: 0;}
    70%{opacity: 0.7;}
    95%{right:40%; top:60%; opacity: 0;}
    100%{right:0%; top:5%; opacity: 0;}
}

@keyframes joinAni8 {
    0%{right:50%; bottom:0%; opacity: 0;}
    5%{opacity: 0.5;}
    95%{right:40%; bottom:80%; opacity: 0;}
    100%{right:50%; bottom:0%; opacity: 0;}
}

@keyframes joinAni9 {
    0%{right:50%; bottom:0%; opacity: 0;}
    5%{opacity: 0.5}
    95%{right:42%; bottom:80%; opacity: 0;}
    100%{right:50%; bottom:0%; opacity: 0;}
}

