.content{

    >figure{
        width:100%;
        height:300px;
        background:#ddd;

        >.inner{
            opacity:0;
            transform: translateY(100px);
            transition:1s;
        }

        .galleryMian{
            opacity:0;
            transform: translateY(100px);
            transition:1s;
        }

        >.contact{
            opacity:0;
            transform: translateY(100px);
            transition:1s;
        }

        .explanation04{
            opacity:0;
            transform: translateY(100px);
            transition:1s;
        }
    }

    > .inner{
        width:1180px;
        margin:0px auto;
        padding-top:100px;
        opacity:0;
        transform: translateY(100px);
        transition:1s;


        h1{
            font:30px/1 'arial';
            color:#333;
            margin-bottom:20px;
        }

        >section{
            min-height:500px;
            padding-bottom: 100px;
        }
    }

    &.on {

        >figure {
            >.inner{
                opacity:1;
                transform:translateY(0%);
            }

            .galleryMian{
                opacity:1;
                transform:translateY(0%);
            }

            >.contact{
                opacity:1;
                transform:translateY(0%);
            }

            .explanation04{
                opacity:1;
                transform:translateY(0%);
            }
        }

        >.inner{
            opacity:1;
            transform:translateY(0%);
        }
    }
}
