.login{
    margin-top:-100px;
    height: 100vh;
    padding:100px 0px 100px;
    position: relative;
    overflow: hidden;
    background-image: url(../../../public/img/login02.jpg);
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat
    ;
    filter: saturate(50%);

    .inner{ 
        width: 500px;
        height: 600px;
        margin: 0 auto;
        padding-top: 0px;
        margin-top: 6%;

        .loginForm{
            background: rgba(0,0,0,.2);
            box-shadow: 5px 5px 10px rgba(0,0,0,.9);
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            padding-bottom: 0px;
            justify-content: space-between;
            align-items: center;
            padding-top: 50px;

            h1{
                font:bold 45px/1 'Orbitron';
                color:#f9bf7cc9;
                letter-spacing: 5px;
            }

            b{
                font:normal 10px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 2px;
                margin-bottom: 20px;
                display: block;
            }

            button{
                font:bold 10px/1 'Orbitron';
                color:rgba(0,0,0,.8);
                letter-spacing: 3px;
                width: 70px;
                height: 25px;
                cursor: pointer;
                transition: 0.5s;
                margin-bottom: 50px;
                border:none;

                &:hover{
                    background: rgba(0,0,0,.5);
                    color:rgba(255,255,255,.8);
                }
            }
            
            table{
                width: 100%;
                display: block;

                tbody{
                }

                tr{
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    padding:0px 50px;
                }
            
                th{
                    width: 100%;
                    height:30px;
                    font:bold 12px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 3px;
                    margin-bottom: 10px;

                    &.btnSet{
    
                        margin-top: 10px;
                        padding:0px 0px 10px;

                        input[type=reset], input[type=submit]{
                            width: 120px;
                            height: 30px;
                            text-align: center;
                            margin:0px 15px 10px;
                            cursor: pointer;
                            transition: 0.5s;
                            border:none;
                            background: #f9bf7c;
                            font:bold 11px/13px 'Orbitron';
                            color:#333;
                            letter-spacing: 1.5px;
            
                            &:hover{
                                background: #111;
                                color:#fff;
                            }
                        }
                    }
                }

                td{
                    width: 100%;
                    
                    input[type=text], input[type=password],select{
                        width: 100%;
                        padding:10px 20px;
                        border:1px solid #aaa;
                        margin-bottom: 30px;
                    }
        
                    label{
                        font:12px/1 'arial';
                        color:#777;
                        margin-right: 5px;
                    }
            
                    .err{
                        font:bold 11px/13px 'Orbitron';
                        color:red;
                        display: block;
                        margin-top:-10px;
                        margin-bottom: 15px;
                        text-align: center;
                        letter-spacing: 1.3px;
                    }
                }
            }        
        }
    }
}

