}

@media screen and(max-width:$tablet) { 
}

@media screen and (max-width: 899px){

}
@media screen and (max-width: 679px){
    .login{
    
        .inner{ 
            width: 90%;
            margin-top: 15%;
    
            .loginForm{
                h1{font:bold 40px/1 'Orbitron';}    
            }
        }
    }
}

@media screen and (max-width: $mobile){ 
    .login{
    
        .inner{ 
            width: 90%;
            margin-top: 35%;
    
            .loginForm{
                justify-content: center;
                padding-top: 0;

                h1{font:bold 35px/1 'Orbitron';}    

                table{
    
                    tr{padding:0px 25px;}
                
                    th{
    
                        &.btnSet{
                            margin-bottom: 30px;

                            input[type=reset], input[type=submit]{
                                width:75px;
                                height: 27px;
                                font:bold 10px/13px 'Orbitron';
                            }
                        }
                    }
                } 
            }
        }
    }
}
