@import url('https://fonts.googleapis.com/css2?family=Shippori+Antique+B1&family=Zhi+Mang+Xing&display=swap');

#map{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/mapBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 50px 0px;
    filter: saturate(70%);
 

    >strong{
        position: absolute;
        font:bold 175px/1 'Orbitron';
        color:rgba(255,255,255,.05);
        letter-spacing: 10px;
        top:-10%;
        right: -300px;
        transform: rotate(90deg);
    }

    .inner{
        margin: 0 auto;
        width: 1180px;
        height: 700px;
        position: relative;
        background: #1e2831;
        padding-left: 45px;
        padding-top: 20px;
        box-shadow: 3px 3px 5px rgba(0,0,0,.9);

        .wrap{
            width: 100%;
            height: 700px;
            padding:50px 0px;
            display: flex;
            flex-direction: row;

            .mapList{
                width: 50%;
                height: 100%;
                padding:50px 0px;

                h2{
                    font:bold 100px/1 'Orbitron';
                    color:rgba(255,255,255,.7);
                    letter-spacing: 10px;
                    position: absolute;
                    top: -50px;
                }

                #maps{
                    width: 100%;
                    height: 100%;
                    filter: grayscale(100%);
                }
            }

            .contactForm{
                width: 50%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                padding-left: 75px;


                h2{
                    font:bold 50px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing:5px;
                    margin-bottom: 30px;
                }

                ul{
                    text-align: left;
                    margin-bottom: 30px;

                    li{
                        strong{
                            font:bold 15px/1 'Orbitron';
                            color:rgba(255,255,255,.8);
                            letter-spacing:2.3px;
                            margin-bottom: 15px;
                            display: block;
                        }

                        p{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                            margin-bottom: 10px;
                        }

                        span{
                            font:normal 12px/1 'Orbitron';
                            color:rgba(255,255,255,.7);
                            letter-spacing:2.3px;
                        }
                    }
                }
                
                h3{
                    font:bold 25px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing:5px;
                    margin-bottom: 25px;
                }

                form{
                    width: 85%;
                    
                    tbody{
                        width: 100%;
                        height: 100%;

                        tr{

                            td{

                                input{
                                    width: 450px;
                                    height: 40px;
                                    margin-bottom: 20px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }

                                textarea{
                                    width: 450px;
                                    margin-bottom: 15px;
                                    font:bold 10px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    color:#111;
                                    padding:10px;
                                }
                            }

                            .btnSet2{

                                input{
                                    width: 100px;
                                    height: 30px;
                                    border:none;
                                    background: rgba(255,255,255,.9);
                                    color:rgba(0,0,0,.9);
                                    cursor: pointer;
                                    font:bold 11px/1 'Orbitron';
                                    letter-spacing:1.8px;
                                    transition: 1s;

                                    &:hover{
                                        background: rgba(0,0,0,.9);
                                        color:rgba(255,255,255,.9);
                                    }
                                }
                            }
                        }
                    }
       
                }
            }
        }
    }

    .custom-shape-divider-top-1645429182 {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        line-height: 0;
        transform: rotate(180deg);
        opacity: 0.5;
    }
    
    .custom-shape-divider-top-1645429182 svg {
        position: relative;
        display: block;
        width: calc(107% + 1.3px);
        height: 200px;
    }
    
    .custom-shape-divider-top-1645429182 .shape-fill {
        fill: #23323b;
        opacity: 0.3;
    }
}



@media screen and(max-width:1559px) {

    #map{
        padding: 50px 20px;
    
        >strong{
            font:bold 150px/1 'Orbitron';
            top:-17.5%;
            right: 20px;
            transform: rotate(0deg);
        }
    
        .inner{
            width: 100%;
    
            .wrap{
                padding:25px 0px 50px;

                .mapList{
    
                    h2{font:bold 85px/1 'Orbitron';}
                }
    
                .contactForm{
    
                    h2{font:bold 45px/1 'Orbitron';}
    
                    ul{
    
                        li{
                            strong{font:bold 13px/1 'Orbitron';}
    
                            p{font:normal 11px/1 'Orbitron';}
    
                            span{font:normal 11px/1 'Orbitron';}
                        }
                    }
                    
                    h3{font:bold 22px/1 'Orbitron';}
    
                    form{
                        
                        tbody{
    
                            tr{
    
                                td{
    
                                    input{width: 400px;}
    
                                    textarea{width: 400px;}
                                }
                            }
                        }
           
                    }
                }
            }
        }
    }
}

@media screen and(max-width:$tablet) { 
    #map{
        padding: 50px 20px;
    
        >strong{font:bold 125px/1 'Orbitron';}
    
        .inner{
            padding-left: 0px;
            padding-top: 0px;

            .wrap{
                width: 100%;
                padding:10px 50px;
                flex-direction: column;

                .mapList{
                    width: 100%;
                    height: 90%;

                    h2{font:bold 75px/1 'Orbitron';}
                }
    
                .contactForm{
                    width: 100%;
                    height: 10%;
                    align-items: flex-end;
                    padding-left: 0px;
                    
        
                    h2{
                        font:bold 75px/1 'Orbitron';
                        color:rgba(255,255,255,.03);
                        letter-spacing:5px;
                        position: absolute;
                        bottom: 0px;
                        left: 50px;
                    }

                    ul{
    
                        li{  
                            text-align: right;

                            strong{
                                font:bold 17px/1 'Orbitron';
                                margin-bottom: 20px;
                            }
    
                            p{font:normal 12px/1 'Orbitron';
                                margin-bottom: 15px;
                            }
    
                            span{font:normal 12px/1 'Orbitron';}
                        }
                    }
                    
                    h3{display: none;}
    
                    form{
                        display: none;
                        tbody{
    
                            tr{
    
                                td{
    
                                    input{width: 400px;}
    
                                    textarea{width: 400px;}
                                }
                            }
                        }
           
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 899px){

    #map{
    
        >strong{font:bold 90px/1 'Orbitron';}
    
        .inner{

            .wrap{

                .contactForm{
        
                    h2{font:bold 50px/1 'Orbitron';}

                    ul{
    
                        li{  

                            strong{font:bold 16px/1 'Orbitron';}
    
                            p{font:normal 11px/1 'Orbitron';}
    
                            span{font:normal 11px/1 'Orbitron';}
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 679px){
    #map{
        height: 100%;
        padding:100px 20px 150px;

        .custom-shape-divider-top-1646765621 svg {height: 800px;}

        >strong{font:bold 70px/1 'Orbitron';}
    
        .inner{
            height: 500px;

            .wrap{
                height: 100%;

                .mapList{
                  
                    h2{
                        font:bold 60px/1 'Orbitron';
                        top:-25px;
                    }
                }

                .contactForm{
        
                    h2{display: none;}

                    ul{
    
                        li{  

                            strong{font:bold 14px/1 'Orbitron';}
    
                            p{font:normal 10px/1 'Orbitron';}
    
                            span{font:normal 10px/1 'Orbitron';}
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile){ 

    #map{
        padding:100px 20px;

        .custom-shape-divider-top-1646765621 svg {height: 700px;}

        >strong{font:bold 45px/1 'Orbitron';}
    
        .inner{
            height: 500px;

            .wrap{
                padding:10px;

                .mapList{
                    margin-bottom: 10px;

                    h2{
                        font:bold 50px/1 'Orbitron';
                        top:-20px;
                    }
                }

                .contactForm{

                    ul{
    
                        li{  

                            strong{font:bold 12px/1 'Orbitron';}
    
                            p{letter-spacing: 1px;}
    
                            span{letter-spacing: 1px;}
                        }
                    }
                }
            }
        }
    }
}
